export function saveToLocal(id,key,value){
	let seller = window.localStorage.__seller__; //得到的是字符串
	if(!seller){
		seller = {};
		seller[id] = {};
	}else{
		seller = JSON.parse(seller);   //JSON.parse(),将字符串转换为对象
		if(!seller[id]){
			seller[id] = {};
		}
	}
	seller[id][key] = value;
	window.localStorage.__seller__ = JSON.stringify(seller);  //JSON.stringify(),将对象转换为字符串
};

export function loadFromLocal(id,key,def){
	let seller =window.localStorage.__seller__;
	if(!seller){
		return def;
	}
	seller = JSON.parse(seller)[id];
	if(!seller){
		return def;
	}
	let ret = seller[key];
	return ret || def;
};



