<template>
  <div>
    <v-header :seller='seller'></v-header>
    <div class="tab border-1px">
      <div class="tab-item">
        <router-link :to="{path:'goods'}">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link :to="{path:'ratings'}">评论</router-link></div>
      <div class="tab-item">
        <router-link :to="{path:'seller'}">商家</router-link></div>
    </div>
    <Keep-alive>
      <router-view :seller="seller"></router-view>
    </Keep-alive>
  </div>
</template>

<script>
  import header from 'components/header/header'
  import {urlParse} from 'commen/js/util'

  const ERR_OK=0

  export default{
    data() {
      return {
        seller: {
          id: (() => {
            let queryParam = urlParse();
            return queryParam.id;
          })()
        }
      };
    },

    // /
    created() {
      this.$http.get('https://www.mocky.io/v2/5aa906fc320000192d165a8d?id='+this.seller.id).then(response => {
          response = response.body;
          this.seller = Object.assign({},this.seller,response.seller);
      },response => {
        console.log('error'+response);
      })
    },
    components: {
      'v-header':header
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
//这里将icon.styl引进来会报错，不知道原因所以将icon图标放在static静态文件夹中，不会被编译
@import 'commen/stylus/index'
  .tab
    display: flex
    width: 100%
    height: 40px
    line-height: 40px
    border-1px(rgba(24,24,120,0.1))
  .tab-item
    flex: 1
    text-align: center
  .tab-item>a
    display: block
    font-size: 14px
    color: rgb(77,85,93)
  .tab-item>a.active
    color: rgb(240,20,20)
</style>



